self.addEventListener('push', (event) => {
    if (event.data) {
        const data = event.data;

        const title = data.title || "New Oscar Notificaton";
        const options = {
            body: data.body || 'This is a push notification from the PWA.',
            icon: data.icon || 'images/icon512.png',
            badge: data.badge || 'images/badge.png',
            vibrate: [200, 100, 200],
            data: {
                url: data.url
            }
        };

        const showNotificationPromise = self.registration.showNotification(title, options);
        event.waitUntil(showNotificationPromise);
    } else {
        console.log('Push event without data received');
    }
});

self.addEventListener('notificationclick', (event) => {
    event.notification.close()

    const action = event.action

    event.waitUntil(
        clients.matchAll({ type: 'window', includeUncontrolled: true }).then((clientList) => {
            for (const client of clientList) {
                if (client.url.includes(self.location.origin) && 'focus' in client) {
                    return client.focus()
                }
            }
            if (clients.openWindow) {
                return clients.openWindow('/')
            }
        })
    )
});